import * as React from "react";
import * as PropTypes from "prop-types";
import { useUID } from "react-uid";
import { StyledIcon } from "@twilio-labs/match-icons-core";

const Refresh = ({
  title,
  decorative = false,
  ...props
}) => {
  const uid = useUID();
  const titleId = title ? uid : undefined;

  if (!decorative && !title) {
    console.warn("[Icon]: Title is required for non-decorative icons.");
  }

  return <StyledIcon {...props} children={<svg viewBox="0 0 24 24" height="100%" width="100%" xmlns="http://www.w3.org/2000/svg" role="img" aria-hidden={decorative} aria-labelledby={titleId}>{title ? <title id={titleId}>{title}</title> : null}<path d="M5.25 14.248L5.25 18.748 0.75 18.748" fill="none" stroke="currentColor" strokeLinecap="round" strokeLinejoin="round" strokeWidth={1.5} /><path d="M18.75 9.748L18.75 5.248 23.25 5.248" fill="none" stroke="currentColor" strokeLinecap="round" strokeLinejoin="round" strokeWidth={1.5} /><path d="M19.032,5.245A9.752,9.752,0,0,1,8.246,21" fill="none" stroke="currentColor" strokeLinecap="round" strokeLinejoin="round" strokeWidth={1.5} /><path d="M4.967,18.751A9.753,9.753,0,0,1,15.754,3" fill="none" stroke="currentColor" strokeLinecap="round" strokeLinejoin="round" strokeWidth={1.5} /></svg>} />;
};

Refresh.propTypes = {
  color: PropTypes.oneOf(["white", "baseRed", "blue50", "blue60", "blue80", "baseGreen", "baseOrange", "basePurple", "gray80", "gray90", "gray100", "currentColor", "inherit"]),
  size: PropTypes.string,
  title: PropTypes.string,
  decorative: PropTypes.bool,
  margin: PropTypes.oneOfType([PropTypes.string, PropTypes.arrayOf(PropTypes.string)]),
  marginTop: PropTypes.oneOfType([PropTypes.string, PropTypes.arrayOf(PropTypes.string)]),
  marginRight: PropTypes.oneOfType([PropTypes.string, PropTypes.arrayOf(PropTypes.string)]),
  marginBottom: PropTypes.oneOfType([PropTypes.string, PropTypes.arrayOf(PropTypes.string)]),
  marginLeft: PropTypes.oneOfType([PropTypes.string, PropTypes.arrayOf(PropTypes.string)])
};
Refresh.defaultProps = {
  color: "gray80",
  size: "base",
  decorative: false
};
Refresh.displayName = "RefreshIcon";
export default Refresh;