import * as React from "react";
import * as PropTypes from "prop-types";
import { useUID } from "react-uid";
import { StyledIcon } from "@twilio-labs/match-icons-core";

const Add = ({
  title,
  decorative = false,
  ...props
}) => {
  const uid = useUID();
  const titleId = title ? uid : undefined;

  if (!decorative && !title) {
    console.warn("[Icon]: Title is required for non-decorative icons.");
  }

  return <StyledIcon {...props} children={<svg viewBox="0 0 24 24" height="100%" width="100%" xmlns="http://www.w3.org/2000/svg" role="img" aria-hidden={decorative} aria-labelledby={titleId}>{title ? <title id={titleId}>{title}</title> : null}<path d="M12 7.5L12 16.5" fill="none" stroke="currentColor" strokeLinecap="round" strokeLinejoin="round" strokeWidth={1.5} /><path d="M7.5 12L16.5 12" fill="none" stroke="currentColor" strokeLinecap="round" strokeLinejoin="round" strokeWidth={1.5} /><path d="M0.750 0.750 L23.250 0.750 L23.250 23.250 L0.750 23.250 Z" fill="none" stroke="currentColor" strokeLinecap="round" strokeLinejoin="round" strokeWidth={1.5} /></svg>} />;
};

Add.propTypes = {
  color: PropTypes.oneOf(["white", "baseRed", "blue50", "blue60", "blue80", "baseGreen", "baseOrange", "basePurple", "gray80", "gray90", "gray100", "currentColor", "inherit"]),
  size: PropTypes.string,
  title: PropTypes.string,
  decorative: PropTypes.bool,
  margin: PropTypes.oneOfType([PropTypes.string, PropTypes.arrayOf(PropTypes.string)]),
  marginTop: PropTypes.oneOfType([PropTypes.string, PropTypes.arrayOf(PropTypes.string)]),
  marginRight: PropTypes.oneOfType([PropTypes.string, PropTypes.arrayOf(PropTypes.string)]),
  marginBottom: PropTypes.oneOfType([PropTypes.string, PropTypes.arrayOf(PropTypes.string)]),
  marginLeft: PropTypes.oneOfType([PropTypes.string, PropTypes.arrayOf(PropTypes.string)])
};
Add.defaultProps = {
  color: "gray80",
  size: "base",
  decorative: false
};
Add.displayName = "AddIcon";
export default Add;